table{
  border-radius:4px;
  font-family: sans-serif, Calibri, Gill Sans, Gill Sans MT,  Trebuchet MS;
  background-color: white;
  table-layout: fixed;
}
table, th, td {
    border-collapse: collapse;
    border-bottom: 2px solid #dcdada;
    text-align:center;
    font-size: 12px;
}
table th{
  font-weight: 600;
  background-color: #F4F4F4;
  border-top:2px solid #dcdada;
  padding: 10px;
}
th ,td{
    width:fit-content;
    min-height:20px;
}

.input-field{
  width:95%;
  height:100%;
  border:none;
  text-align: center;
  outline:none;
}

.input-field:focus,

.input-field:active{
  border:1px solid rgb(91, 85, 85) !important;
}
